<template>
    <!-- -->
    <div class="containers-container">
        <div class="filter-container">
            <!--<el-form id="uploadForm" enctype="multipart/form-data">
                <el-input type="file" name="filename" class="fileinput btn-lg" @change="test"/>
                <el-button type="button" @click="onUpload" class="fileinput_button btn btn-lg btn-success"></el-button>
            </el-form> -->

            <form id="uploadForm" enctype="multipart/form-data">
                <input type="file" name="filename">
                <el-button type="button" @click="onUpload" class="fileinput_button btn btn-lg btn-success"></el-button>
            </form>
            <!--el-input v-model="tableQuery.id" @keyup.enter.native="handleFilter" style="width: 200px;" placeholder="ID"></el-input>
            <el-button style="margin-left: 10px;" @click="getList()" type="primary"><i class="el-icon-search"></i></el-button>
            <el-button @click="deleteRecord(deletedForm.id)" type="primary"><i class="el-icon-delete"></i></el-button-->
            <el-button @click="showDialog('xj')" type="primary"><i class="el-icon-info"></i></el-button>
            <el-button @click="showDialog('xg',createdForm)" type="primary"><i class="el-icon-info"></i></el-button>
            <el-button @click="getExcel()" type="primary"><i class="el-icon-info"></i></el-button>
        </div>
        <!-- -->
        <el-table :data="tableT" @row-click="handlecellclick" v-loading.body="tableLoading" element-loading-text="" stripe border fit highlight-current-row style="width: 100%">
            <el-table-column label="" prop="id" align="center"></el-table-column>
            <el-table-column label="" prop="time" align="center"></el-table-column>
            <el-table-column label="" prop="usercode" align="center"></el-table-column>
            <el-table-column label="" prop="username" align="center"></el-table-column>
            <el-table-column label="" prop="stime" align="center"></el-table-column>
            <el-table-column label="" prop="etime" align="center"></el-table-column>
            <el-table-column label="" prop="outdesc" align="center"></el-table-column>
            <el prop="creater" align="center"></el>
            <el prop="creattime" align="center"></el>
        </el-table>
        <el-dialog :title="formTitle" :visible.sync="formVisible" >
            <el-form class="small-space" :model="createdForm" label-position="left" label-width="70px" style='width: 60%; margin-left:0px;'>
                <el-form-item label="">
                    <el-input v-model="createdForm.id" placeholder="" disabled="disabled"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="createdForm.time" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="createdForm.usercode" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="createdForm.username" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="createdForm.stime" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="createdForm.etime" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="createdForm.outdesc" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="formVisible = false"> </el-button>
                <el-button type="primary" :loading="formSubmiting" @click="commitForm"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import { confirmBox } from 'utils/message';
    export default {
        data() {
            return {
                tableQuery: {
                    limit: 10,
                    currentPage: 1,
                    id: null,
                },
                total: null,
                tableT: [],
                tableLoading: true,
                formTitle: null,
                formVisible: false,
                formSubmiting: false,
                createdForm: {
                    id: null,
                    time: null,
                    usercode: null,
                    username: null,
                    stime: null,
                    etime: null,
                    outdesc: null,
                },
                deletedForm: {
                    id: null
                },
                };
        },
        mounted() {
            this.getList();
        },
        methods: {
            getList() {//
                this.tableLoading = true;
                this.$api.system.wcheck.cx({
                    data: this.tableQuery
                }).then(response => {
                    this.total = response.data.data.total;
                    this.tableT = response.data.data.cx;
                    this.tableLoading = false;
                }).catch(error => {
                    this.tableLoading = false;
                    console.log(error);
                });
            },
            handlecellclick(val) {
                this.deletedForm.id=val.id
                this.createdForm=val
            },
            deleteRecord(id) {
                confirmBox('').then(() => {
                    this.$api.system.wcheck.sc({
                        data: this.deletedForm
                    }).then(response => {
                        this.$notify({
                            title: '',
                            message: '',
                            type: 'success',
                            duration: 1500,
                        });
                        this.getList();
                        console.log(response);
                    }).catch(error => {
                        this.formSubmiting = false;
                        this.$notify({
                            title: '',
                            message: error,
                            type: 'error',
                            duration: 3000,
                        });
                        console.log(error);
                    });
                });
            },
            //dilog
            clearDialog() {
                this.createdForm.id = null;
                this.createdForm.time = null;
                this.createdForm.usercode = null;
                this.createdForm.username = null;
                this.createdForm.stime = null;
                this.createdForm.etime = null;
                this.createdForm.outdesc = null;
            },
            showDialog(type,row){//dilog
                console.log(type);
                this.formVisible = true;//dilog
                if (type == 'xj') {//
                    this.clearDialog();//
                    this.formTitle = "";
                } else {//
                    this.formTitle = "";
                    if(row.id==null){
                        alert('')
                        this.formVisible = false;//dilog
                    }
                }
                this.getList();
            },
            commitForm() {
                this.formSubmiting = true;
                //this.createdForm.creattime=new Date().getFullYear()+'-'+new Date().getMonth()+'-'+new Date().getDay()
                this.$api.system.wcheck.save({
                    data: this.createdForm
                }).then(response => {
                    this.formSubmiting = false;
                    this.formVisible = false;
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success',
                        duration: 1500,
                    });
                    this.getList();
                    console.log(response);
                }).catch(error => {
                    this.formSubmiting = false;
                    this.$notify({
                        title: '',
                        message: error,
                        type: 'error',
                        duration: 3000,
                    });
                    console.log(error);
                });
            },onUpload: function (e) {
                this.$api.system.wcheck.import({
                    data: new FormData($('#uploadForm')[0]),
                }).then(response => {
                    this.formSubmiting = false;
                    this.formVisible = false;
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success',
                        duration: 1500,
                    });
                    this.getList();
                }).catch(error => {
                    console.log(error);
                });
            },getExcel() {
                this.$api.system.wcheck.export({
                    data: this.tableQuery,
                }).then(response => {
                     require.ensure([], () => {
                         const { export_json_to_excel } = require('../../vendor/Export2Excel')
                         const tHeader = ['', '','','','','','']
                         const filterVal = ['id','time','usercode', 'username','stime','etime','outdesc']
                         const list = response.data.data.cx
                         const data = this.formatJson(filterVal, list)
                         export_json_to_excel(tHeader, data, '')
                     }).catch(err => {
                         console.log("");
                   });
                    this.formSubmiting = false;
                    this.formVisible = false;
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success',
                        duration: 1500,
                    });
                }).catch(error => {
                    console.log(error);
                });

            },
            formatJson(filterVal, jsonData) {
                return jsonData.map(v => filterVal.map(j => v[j]))
            },
            expt(){
                const data = [{A:'1111',B:'2222'},{A:'1111',B:'2222'},{A:'1111',B:'2222'}]
                const header = {header: ['A','B'] }
                const xlsxName='1111'
                const outputXlsxFile = (data,header,xlsxName) => {

                    const ws = XLSX.utils.json_to_sheet(data, header)

                    const wb = XLSX.utils.book_new()

                    XLSX.utils.book_append_sheet(wb, ws, xlsxName)

                    XLSX.writeFile(wb, xlsxName + ".xlsx")

                }

                Vue.prototype.$outputXlsxFile
                    = outputXlsxFile

                Vue.prototype.$getExcelFileData = getExcelFileData
            }
        }
    };
</script>



