<template>
	<div class="components-container">{{shortMSG}}
		<div class="filter-container">
			<el-button @click="queryList" type="gray"><i class="el-icon-refresh"></i></el-button>
            <el-button @click="restfulAuth" type="gray"><i class="el-icon-refresh"></i>client</el-button>
            <el-button @click="testRestFul" type="gray"><i class="el-icon-refresh"></i>client</el-button>
		</div>
		<el-table :data="tableData" v-loading.body="tableLoading" element-loading-text="" stripe border fit highlight-current-row style="width: 100%">
		    <el-table-column v-if="tableColumShow" label="" prop="sm_URL" align="center" sortable ></el-table-column>
		    <el-table-column label="" prop="c_ID" align="center" ></el-table-column>
		    <el-table-column label="" prop="c_Name" align="center" ></el-table-column>
			<el-table-column label="" prop="c_State" align="center"></el-table-column>
		</el-table>
	</div>
</template>

<script>
	import { confirmBox } from 'utils/message';
	export default {
	    data() {
	        return {
				//
	            shortMSG:"custom.vue",
				tableColumShow:false,
				tableQuery: {  //
				    limit: 10,//
				    currentPage: 1,//
				    frontArg: ""
				},
				querySubmiting:false,
				queryForm:false,
				total: 0,
				tableData: [],//
				tableLoading: false//
	        }
	    },
	    methods: {
			//
			changeMSG(){
				this.shortMSG=""
			},
			queryList(){
				this.tableLoading = true;
				//api
				this.$api.custom.info.list({
					//
				    data: this.tableQuery
				}).then(response => {
					//
				    this.queryForm = false;
				    this.tableLoading = false;
					//
					console.log(response)
					this.tableData = response.data.data.list
					this.shortMSG = response.data.message
				}).catch(error => {
					//
				    this.tableLoading = false;
					//
				    console.log(error);
				});
			},
            restfulAuth(){
                //api
                this.$api.custom.restful.auth({
                	//
                    data: this.tableQuery
                }).then(response => {
                	//
                	console.log(response)
                    this.shortMSG = response.data.message
                    this.$message.success(this.shortMSG)
                }).catch(error => {
                	//
                    console.log(error);
                    this.shortMSG = error
                    this.$message.error(this.shortMSG)
                });
            },
            testRestFul(){
                //api
                this.$api.custom.restful.test({
                	//
                    data: this.tableQuery
                }).then(response => {
                	//
                	console.log(response)
                    this.tableData = response.data.data.list
                    this.shortMSG = response.data.message
                    this.$message.success(this.shortMSG)
                }).catch(error => {
                	//
                    console.log("error:"+error);
                    this.tableData = []
                    this.shortMSG = error
                    this.$message.error(this.shortMSG)
                });
            }
	    },
	    mounted() {
	        //
			this.changeMSG()
	    },

	}
</script>

<style>
</style>



