<template>
        <div class="components-container">
        <div class="filter-container">
            <el-input v-model="tableQuery.deptCode" @keyup.enter.native="handleFilter" style="width: 200px;" placeholder=""></el-input>
            <el-input v-model="tableQuery.cname" @keyup.enter.native="handleFilter" style="width: 200px;" placeholder=""></el-input>
            <el-select v-model="tableQuery.state" clearable placeholder="">
                <el-option v-for="item in state" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
            <el-button style="margin-left: 10px;" @click="handleFilter" type="gray"><i class="el-icon-search"></i></el-button>
            <el-button @click="handleReset" type="gray"><i class="el-icon-refresh"></i></el-button>
            <el-button @click="showDialog('create')" type="gray"><i class="el-icon-plus"></i> </el-button>
        </div>

        <el-table :data="tableData" v-loading.body="tableLoading" element-loading-text="" stripe border fit highlight-current-row style="width: 100%">
            <el-table-column label="" prop="deptCode" align="center"></el-table-column>
            <el-table-column label="" prop="cname" align="center"></el-table-column>
            <el-table-column label="" prop="state" align="center">
                <template slot-scope="scope">                                      <!-- statusMap -->
                    <el-tag :type="scope.row.state | stateFilter">{{scope.row.state | statusMap}}</el-tag>
                </template>                     <!-- stateFilter -->
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button size="small" @click="deptMeunManage(scope.row.deptCode)" type="primary" icon="setting"></el-button>
                    <el-button size="small" @click="showDialog('update', scope.row)" type="gray"><i class="el-icon-edit"></i> </el-button>
                    <el-button size="small" @click="deleteRecord(scope.row.deptCode)" type="danger"><i class="el-icon-delete"></i> </el-button>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination-container">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="tableQuery.currentPage"
                :page-sizes="[10, 20, 50]" :page-size="tableQuery.limit" layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

        <el-dialog :title="formTitle" :visible.sync="formVisible">
            <el-form class="small-space" :model="createdItem" :rules="createdRules" label-position="left" label-width="70px" style='width: 60%; margin-left:0px;'>
                <el-form-item label="">
                    <el-input v-model="createdItem.deptCode" placeholder="" v-bind:disabled="formTitle!=''" ></el-input>
                </el-form-item>
                <el-form-item label="" prop="cname">
                    <el-input name="cname" v-model="createdItem.cname" autoComplete="on" placeholder="" ></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="createdItem.state" clearable placeholder="">
                        <el-option v-for="item in state" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="formVisible = false"> </el-button>
                <el-button type="warning" :loading="formSubmiting" @click="commitForm"> </el-button>
            </div>
        </el-dialog>
        <menu-manage ref="menuManage"> </menu-manage>
    </div>
</template>

<script>
import { confirmBox } from 'utils/message';                       //
import { isEmail, isPhone } from 'utils/validate';
import MenuManage from 'views/system/roleOperate/DeptMenuManage';
export default {
    components: {
        MenuManage,
    },
    data() {
         const validateCName = (rule, value, callback) => {              //
            if (value=="") {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        return {
            tableQuery: {                      //
                limit: 10,
                currentPage: 1,
                deptCode: null,
                cname: null,
                state: null
            },
            createdRules: {
                cname: [  //name
                    { required: false, trigger: 'blur', validator: validateCName }
                ]
            },
            total: null,
            tableData: [],
            state: [                    //value
                {
                    label: '',
                    value: "Y"
                },
                {
                    label: '',
                    value: "N"
                }
            ],
            tableLoading: true,
            formTitle: null,
            formVisible: false,
            formSubmiting: false,
            createdItem: {
                deptCode: null,
                cname: null,
                state: null,
            },
            deletedForm: {
                deptCode: null
            },

        };
    },
    filters: {
        stateFilter(state) {
            const statusMap = {       //
                'Y': 'primary',      //  
                'N': 'danger',       //  
            };
            return statusMap[state]
        },
        statusMap(state) {
            const statusMap = {
                'Y': '',        //
                'N': '',
            };
            return statusMap[state]
        },
        roleNameMap(roleId) {
            const roleNameMap = {};
            this.roleList.forEach(function (role) {
                roleNameMap.push()
            })
        }
    },

    mounted() {

        this.getUserList();
    },
    methods: {
        deptMeunManage(id) {
            this.$refs.menuManage.getDeptMenu(id);
        },
        getUserList() {                    //
            this.tableLoading = true;
            this.$api.system.department.list({         //api
                data: this.tableQuery
            }).then(response => {
                this.total = response.data.data.total;
                this.tableData = response.data.data.list;
                this.tableLoading = false;
            }).catch(error => {
                this.tableLoading = false;
                console.log(error);
            });
        },
        handleFilter() {                       //
            this.getUserList();
        },
        handleReset() {
            this.tableQuery.limit = 10;
            this.tableQuery.currentPage = 1;
            this.tableQuery.deptCode = null;
            this.tableQuery.cname = null;
            this.tableQuery.state = null;
            this.formSubmiting = false;
            this.formVisible = false;
            this.getUserList();
        },
        handleSizeChange(val) {
            this.tableQuery.limit = val;
            this.getUserList();
        },
        handleCurrentChange(val) {
            this.tableQuery.currentPage = val;
            this.getUserList();
        },
        clearDialog() {            //
            this.createdItem.deptCode = null;
            this.createdItem.cname = null;
            this.createdItem.state = null;

        },
        showDialog(type, row = null) {
            this.formVisible = true;
            this.clearDialog();
            if (type == 'create') {
                this.formTitle = "";           //
            } else {
                this.formTitle = "";
                //createdItem
                this.createdItem.deptCode = row.deptCode;   //row
                this.createdItem.cname = row.cname;
                this.createdItem.state = row.state;
            }
        },
        commitForm() {                                //
            this.formSubmiting = true;
            this.$api.system.department.save({
              data: this.createdItem
            }).then(response => {
                this.formSubmiting = false;
                this.formVisible = false;
                this.$notify({
                    title: '',
                    message: '',
                    type: 'success',
                    duration: 1500,
                });
                this.handleFilter();
            }).catch(error => {
                this.formSubmiting = false;
                this.$notify({
                    title: '',
                    message: error,
                    type: 'error',
                    duration: 3000,
                });
                console.log(error);
            });
        },            //ID
        deleteRecord(DeptCode) {                                //
            confirmBox('').then(() => {
                         //deptCodedeptCode
                this.deletedForm.deptCode = DeptCode;//ID
                this.$api.system.department.delete({
                    data: this.deletedForm
                }).then(response => {
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success',
                        duration: 1500,
                    });
                    this.handleFilter();
                }).catch(error => {
                    this.formSubmiting = false;
                    this.$notify({
                        title: '',
                        message: error,
                        type: 'error',
                        duration: 3000,
                    });
                    console.log(error);
                });
            });
        },

    },
};
</script>



