<template>
     <div class="components-container">
         <div class="components-container">
            <div class="filter-container">
                <el-button @click="getApplyList" type="gray"><i class="el-icon-refresh"></i></el-button>
                <el-button @click="openCreateDialog('create')" type="gray"><i class="el-icon-plus"></i></el-button>
                <el-button @click="openCreateDialog('edit')" type="gray"><i class="el-icon-edit"></i></el-button>
                <el-button @click="queryForm = true" type="gray"><i class="el-icon-search"></i></el-button>
            </div>
            <el-table :data="tableData" v-loading.body="tableLoading" element-loading-text=""
                stripe border fit highlight-current-row style="width: 100%" @row-click="rowClick">
                <el-table-column v-if="tableColumShow" label="" prop="sft_Code" align="center" sortable ></el-table-column>
                <el-table-column label="" prop="sft_TemplateType" align="center"></el-table-column>
                <el-table-column label="" prop="sft_State" align="center" width="250px">
                    <template slot-scope="scope">
                        <span>{{scope.row.sft_State | stateMap}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" width="200px">
                    <template slot-scope="scope">
                        <el-button size="small" v-if="scope.row.sft_State=='0'" @click="changeState(scope.row)" type="success">
                            <i class="el-icon-circle-check"></i>
                        </el-button>
                        <el-button size="small" v-if="scope.row.sft_State=='1'" @click="changeState(scope.row)" type="danger">
                            <i class="el-icon-circle-close"></i>
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination-container">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="tableQuery.currentPage"
                    :page-sizes="[10, 20, 50]" :page-size="tableQuery.limit" layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
            <el-dialog :title="''" :visible.sync="queryForm">
                <el-form class="small-space" :model="tableQuery" label-position="left" label-width="100px" style='width: 100%; margin-left:0px;'>
                    <el-form-item label="">
                        <el-input name="sft_TemplateType" v-model="tableQuery.sft_TemplateType" autoComplete="on" maxlength="50"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="tableQuery.sft_State" placeholder="" style="width: 100%">
                            <el-option v-for="item in stateArr" :key="item.value" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="queryForm = false"> </el-button>
                    <el-button type="warning" :loading="querySubmiting" @click="getApplyList"> </el-button>
                </div>
            </el-dialog>
            <el-dialog :title="formTitle" :visible.sync="createForm" :before-close="closePreview">
                <el-form class="small-space" :model="tableCreate" label-position="left" label-width="100px" style='width: 100%; margin-left:0px;'>
                    <el-form-item label="" :required=true>
                        <el-input name="sft_TemplateType" v-model="tableCreate.sft_TemplateType" autoComplete="on" maxlength="50"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="tableCreate.sft_State" placeholder="" style="width: 100%">
                            <el-option v-for="item in stateArr" :key="item.value" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <div v-if="tableCreate.sft_Code != null && tableCreate.sft_State=='1'">
                        <el-form :model="form">
                          <el-form-item label="" :label-width="formLabelWidth">
                            <el-upload
                              ref="uploadExcel"
                              :action="uploadPath"
                              :limit=limitNum
                              :auto-upload="true"
                              :before-upload="beforeUploadFile"
                              :on-change="fileChange"
                              :on-exceed="exceedFile"
                              :on-success="handleSuccess"
                              :on-error="handleError"
                              :on-preview="previewFile"
                              :on-remove="removeFile"
                              :before-remove="removeAlert"
                              :data="otherData"
                              :with-credentials='true'
                              accept=".doc,.docx,.xls,.xlsx,.pdf"
                              :file-list="fileArr">
                              <el-button size="small" type="success"></el-button>
                              <span style="color:red"> *docdocxxlsxlsxpdf </span>
                            </el-upload>
                          </el-form-item>
                          <span>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&nbsp;</span>
                          <el-button size="small" type="info" @click="download()"></el-button>
                          <el-button size="small" type="warning" @click="previewPdf()"></el-button>
                        </el-form>
                        <div v-if="isPreview">
                            <iframe id='previewPdf' :src="pdfUrl" height="560" width="100%"></iframe>
                        </div>
                      </div>
                    <div v-else>
                        <el-form-item label="">
                            <el-button size="small" type="success" disabled></el-button><span style="color: red;"> * </span>
                        </el-form-item>
                    </div>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="createForm = false;isPreview = false;"> </el-button>
                    <el-button type="warning" :loading="querySubmiting" @click="saveCreate"> </el-button>
                </div>
            </el-dialog>
         </div>
    </div>
</template>

<script>
import { confirmBox } from 'utils/message';
import { isJson } from 'utils';

export default {
    data() {
        return {
            formTitle:'',
            createForm:false,
            tableColumShow:false,
            tableQuery: {   //
                limit: 10,
                currentPage: 1,
                sft_TemplateType: null,
                sft_State: null
            },
            tableCreate: {  //
                sft_Code: null,
                sft_TemplateType: null,
                sft_State: null,
            },
            stateArr: [ //
                {value: '0', label: ''},
                {value: '1', label: ''}
            ],
            querySubmiting:false,
            queryForm:false,
            total: 0,
            tableData: [],  //
            tableLoading: false,
            stateChange: {  //
                sft_Code: null,
                sft_State: null
            },
            getFfile:{
               sf_BussinessCode:null
            },
            fileList:[],
            //
            limitNum: 20,
            formLabelWidth: '80px',
            form: {
              file: ''
            },
            fileID:'',
            pdfUrl:'',
            otherData:{fileID:null},
            isPreview:false,
            uploadPath:this.HOST_PROXY_ADMIN+'/file/file/upload',
            fileArr:[],
            deleteData:{
                sf_Code:null,
            }
        }
    },
    methods: {
        openCreateDialog(action){
            if(action=="edit"){
                if(this.tableCreate.sft_Code==null){
                    this.$notify({
                        title: '',
                        message:"",
                        type: 'warning',
                        duration: 2000,
                    });
                    return;
                }
                this.getFileArr();
                this.createForm = true;
            }else{
                this.clearCreateDialog();  //
                this.createForm = true;
            }
        },
        getFileArr(){
            this.getFfile.sf_BussinessCode = this.tableCreate.sft_Code;
            this.$api.file.template.filelist1({
                data:this.getFfile
            }).then(response => {
                this.fileList = response.data.data.Filelist;
                this.getFileList();
            }).catch(error => {
                console.log(error);
            });
        },
        clearCreateDialog(){
            this.tableCreate.sft_Code = null;
            this.tableCreate.sft_TemplateType = null;
            this.tableCreate.sft_State = null;
        },
        changeState(row){
            if(row.sft_State=="0"){
                row.sft_State="1";
            }else if(row.sft_State=="1"){
                row.sft_State="0";
            }
            this.$api.file.template.save({
                data: row
            }).then(response => {
                let succeed = response.data.data.Succeed;
                if(succeed=="Succees"){
                    this.$notify({
                        title: '',
                        message:"",
                        type: 'success',
                        duration: 2000,
                    });
                }else{
                   this.$notify({
                       title: '',
                       message:"",
                       type: 'error',
                       duration: 2000,
                   });
                }
            }).catch(error => {
                this.$notify({
                    title: '',
                    message:"",
                    type: 'error',
                    duration: 2000,
                });
                console.log(error);
            });
        },
        saveCreate(){
             this.$api.file.template.save({
                 data: this.tableCreate
             }).then(response => {
                let succeed = response.data.data.Succeed;
                 if(succeed=="Succees"){
                     this.createForm = false;
                     this.$notify({
                         title: '',
                         message:"",
                         type: 'success',
                         duration: 2000,
                     });
                     this.getApplyList();
                 }else{
                    this.$notify({
                        title: '',
                        message:"",
                        type: 'error',
                        duration: 2000,
                    });
                 }
             }).catch(error => {
                 this.$notify({
                     title: '',
                     message:"",
                     type: 'error',
                     duration: 2000,
                 });
                 console.log(error);
             });
        },
        getApplyList(){
            this.tableLoading = true;
            this.$api.file.template.list({
                data: this.tableQuery
            }).then(response => {
                this.total = response.data.data.total;
                this.tableData = response.data.data.list;
                this.queryForm = false;
                this.tableLoading = false;
                this.clearCreateDialog();
            }).catch(error => {
                this.tableLoading = false;
                console.log(error);
            });
        },
        rowClick(row){
            this.tableCreate.sft_Code = row.sft_Code;
            this.tableCreate.sft_TemplateType = row.sft_TemplateType;
            this.tableCreate.sft_State = row.sft_State;
        },
        handleCurrentChange(val) {
            this.tableQuery.currentPage = val;
            this.getApplyList();
        },
        handleSizeChange(val) {
            this.tableQuery.limit = val;
            this.getApplyList();
        },
        // 
        exceedFile(files, fileList) {
          this.$notify.warning({
            title: '',
            message: ` ${this.limitNum}  ${files.length + fileList.length} `
          });
        },
        // 
        fileChange(file, fileList) {
          this.form.file = file.raw;

        },
        // , , false  Promise  reject
        /* beforeUploadFile(file) {
            this.otherData.fileID = this.tableCreate.sft_Code;
        }, */
        beforeUploadFile(file) {
            let name = file.name;
            let type = name.substring(name.lastIndexOf('.'),name.length);
            if(type==".doc"||type==".pdf"||type==".docx"||type==".xls"||type==".xlsx"){
                    this.otherData.fileID = this.tableCreate.sft_Code;
                    this.$parent.$parent.$parent.hideOrShow(); //
            }else{
                file['create'] = 'true';
                return false;
            }
        },
        // 
        handleSuccess(res, file, fileList) {
          this.$notify.success({
            title: '',
            message: ``
          });
          this.getFileArr(); //
          this.$parent.$parent.$parent.hideOrShow(); //
        },
        // 
        handleError(err, file, fileList) {
          this.$notify.error({
            title: '',
            message: ``
          });
        },
        uploadFile() {
           this.$refs.uploadExcel.submit()
        },
        download(){
            if(this.fileID==null || this.fileID==""){
                this.$notify({
                    title: '',
                    message:"",
                    type: 'warning',
                    duration: 2000,
                });
                return;
            }
            var blob = new Blob([],{type:'application/excel;charset=utf-8'})
        var downloadElement = document.createElement('a')
        var href = window.URL.createObjectURL(blob) //
       // let sendParams = Base64.encode(JSON.stringify('')) //  
        downloadElement.href = this.HOST_PROXY_ADMIN+'/file/file/download?id='+this.fileID //'' 
        document.body.appendChild(downloadElement)
        downloadElement.click() // 
        document.body.removeChild(downloadElement) // 
       // window.URL.removeObjectURL(href) // blob
           // this.URL.removeObjectURL(href) // blob
        },
        getFileList(){
            this.fileArr = [];
            setTimeout(() =>{
                for(var i = 0;i<this.fileList.length;i++){
                    let file={id:"",name:""};
                    file.id=this.fileList[i].sf_Code;
                    file.name=this.fileList[i].sf_FileName;
                    this.fileArr.push(file)
                }
            },1000);
        },
        previewFile:function(file){
            this.fileID=file.id;
        },
        closePreview(){
            this.fileID = null;
            this.isPreview = false;
            this.createForm = false;
        },
        previewPdf(){
            if(this.fileID==null || this.fileID==""){
                this.$notify({
                    title: '',
                    message:"",
                    type: 'warning',
                    duration: 2000,
                });
                return;
            }
            this.isPreview = true;
            //
            let url=this.HOST_PROXY_ADMIN+'/file/file/preview?fileID='+this.fileID
            this.pdfUrl=this.FONT_PROXY_PATH+"/static/pdf/web/viewer.html?url_file="+url
        },
        removeFile:function(file, fileList){
            this.deleteData.sf_Code = file.id;
            this.$api.file.file.delete({
              data: this.deleteData
            }).then(response => {
                let succeed = response.data.data.Succeed;
                if(succeed=="Succees"){
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success',
                        duration: 1500,
                    });
                }else{
                    this.$notify({
                        title: '',
                        message: '',
                        duration: 1500,
                    });
                }
            }).catch(error => {
                this.$notify({
                    title: '',
                    message: error,
                    type: 'error',
                    duration: 3000,
                });
                console.log(error);
            });
        },
        removeAlert:function(file, fileList){
            if(file.raw!=undefined && file.raw.create != undefined){  //
                this.$notify({
                    title: '',
                    message: '',
                    type: 'error',
                    duration: 3000,
                });
                this.fileArr = fileList.filter(function(element, index) {
                    return element['uid'] != file.uid;
                });
                return false;
            }
            return confirmBox(file.name).then(() => { })
        }
        /* removeAlert:function(file, fileList){
            return confirmBox(file.name).then(() => { })
        } */
    },

    filters: {
        stateMap(state) {
            const stateMap = {
                '0':'',
                '1':'',
            };
            return stateMap[state]
        },
    },
    mounted() {
        this.getApplyList();
    },
}
</script>

<style>
</style>



